import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MainComponent } from './main/main.component';
import {  mainDashboardRes} from './mainDashboardRes/mainDashboardRes.component';

import { Dashboard2Component } from './dashboard2/dashboard2.component';
import { Dashboard3Component } from './dashboard3/dashboard3.component';
import { projectSelectionComponent } from './projectSelection/projectSelection.component';


const routes: Routes = [
  {
    path: '',
    redirectTo: 'mainRes',
    pathMatch: 'full'
  },

  {
    path: 'mainProject',
    component: projectSelectionComponent
  },

  {
    path: 'mainRes',
    component: mainDashboardRes
  },
  {
    path: 'main',
    component: MainComponent
  },
  {
    path: 'dashboard2',
    component: Dashboard2Component
  },
  {
    path: 'dashboard3',
    component: Dashboard3Component
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DashboardRoutingModule {}
